import { Box, Flex, Separator } from "@chakra-ui/react";
import Sidebar from "./Sidebar";
import Topbar from "./Topbar";

interface DashboardLayoutProps {
  children: React.ReactNode;
}

export default function DashboardLayout({ children }: DashboardLayoutProps) {
  return (
    <Flex 
      minH="100vh" 
      bg="gray.50" 
      _dark={{ bg: "gray.950" }} // Slightly deeper dark for contrast
    >
      {/* Sidebar: 
        Hidden on mobile (mdDown), fixed width on desktop.
      */}
      <Box 
        display={{ base: "none", md: "block" }} 
        w="280px" 
        borderEndWidth="1px"
        bg="white"
        _dark={{ bg: "gray.900", borderEndColor: "gray.800" }}
      >
        <Sidebar />
      </Box>

      {/* Main Container */}
      <Flex direction="column" flex="1" minW={0}>
        {/* Topbar:
          Sticky to the top so it stays while scrolling.
        */}
        <Box 
          position="sticky" 
          top="0" 
          zIndex="docked" 
          bg="white/80" 
          backdropFilter="blur(10px)"
          _dark={{ bg: "gray.900/80" }}
        >
          <Topbar />
          <Separator />
        </Box>

        {/* Page Content: 
          Max-width added for better readability on huge monitors.
        */}
        <Box
          as="main"
          p={{ base: 4, md: 8 }}
          flex="1"
          mx="auto"
          w="full"
          maxW="8xl" // Prevents content from stretching too far
        >
          {children}
        </Box>
      </Flex>
    </Flex>
  );
}